<template>
    <div>
        <div class="desmos_comp-w">

            <div class="desmos_search-w is--template-border w-form">
                <input
                    v-model="searchInput"
                    @keyup="onSearchComponents"
                    type="search"
                    class="desmos_comp-search w-input large-blur"
                    maxlength="256"
                    name="query"
                    placeholder="Desmos Expressions"
                    ref="search"
                    required=""
                >
                <input type="submit" value="" class="search_button-4 w-button large-blur">
            </div>
            <!-- <a href="#" class="desmos_settings-linkw is--template-border w-inline-block"><img src="../images/color-palette.svg" loading="lazy" alt="" class="desmos_settings-img is--color large-blur"></a> -->

            <div
                v-if="searchInput.length > 0"
                @click="searchInput = ''"
                class="desmos_comp-menu-close"
            ></div>
            <div
                class="desmos_comp-menu"
                v-show="searchInput.length > 0"
            >
                <a v-if="!createComp" @click="onCreateComp" href="#" class="button is--large is--bottom-margin w-button">Create Custom Component</a>
                <div class="fullwidthline is--thin"></div>
                <h3 class="is--center is--bottom-margin">Most Relevant</h3>
                <div
                    ref="scrollContainer"
                >
                    <div
                        v-for="(comp, index) in resultComponents"
                        :key="index"
                        :id="comp.id"
                        @click="onClickToComponent(comp)"
                        :class="index === arrowCounter ? 'is--template-border': ''"
                        class="desmos_comp-item"
                        ref="options"
                    >
                        <div class="desmos_comp-tags-w">
                            <div class="text-block-11">{{ comp.expressions.length }} Expression</div>
                            <div
                                v-if="comp.expressions.length > 1"
                                class="text-block-12 is--utility"
                            >
                                Utility
                            </div>
                            <div

                                v-else
                                class="text-block-12"
                            >
                                General
                            </div>
                        </div>
                        <h4>{{ comp.title }}</h4>
                        <div>{{ comp.description }}</div>
                    </div>
                </div>
            </div>
            <div class="desmos_comp-create" :style="createComp ? 'display: block;': ''">
                <a v-if="createComp" @click="createComp = false" href="#" class="">
                    <i class="fa fa-times"></i>
                </a>
                <div class="w-form">
                <div id="desmosCompForm" name="email-form-2" data-name="Email Form 2" class="desmos-comp-form">
                    <h3 class="is--center">General Info</h3>
                    <div class="fullwidthline is--thin"></div>
                    <input v-model="title" type="text" class="input-field w-input" maxlength="256" name="Name" data-name="Name" placeholder="Name" id="Name">
                    <textarea v-model="description" data-name="Description" maxlength="5000" id="Description" name="Description" placeholder="Component Description" class="input-field is--text-area w-input"></textarea>
                    <uflo-select
                        v-model="componentType"
                        title="Component Type"
                        :options="['Data', 'Function', 'Utility']"
                    >
                    </uflo-select>
                    <h3 class="is--center">Select Expressions</h3>
                    <div class="fullwidthline is--thin"></div>

                    <div
                        v-for="(exp, index) in existComponents"
                        :key="index"
                        class="wrapper is--vertical-grid"
                    >
                        <div
                            class="select-comp-expression"
                            @click="exp.expressionSelected = !exp.expressionSelected"
                            :class="exp.expressionSelected ? 'is--select': ''"
                        >
                            <div class="latex-render w-embed">
                            <div>{{ '$$'+ exp.latex +'$$' }}</div>
                            </div>
                            <div class="comp_expr-select"></div>
                        </div>
                        <label class="w-checkbox checkbox-field">
                            <input
                                @click="onClickCheckDependency(index, exp.dependencyChecked)"
                                type="checkbox"
                                name="checkbox"
                                data-name="Checkbox"
                                class="w-checkbox-input"
                                :value="exp.dependencyChecked"
                            >
                           <span class="w-form-label">Expression dependant on another expression</span>
                        </label>
                        <div v-show="exp.dependencyChecked">
                            <uflo-select-with-create
                                v-on:selected="(value) => {exp.dependencyName = value.name}"
                                v-on:update-option="(value) => {dependencyOptions = value}"
                                v-model="exp.dependencyName"
                                placeholder="Select Dependancy"
                                :options="dependencyOptions"
                            >
                            </uflo-select-with-create>
                            <input
                                :value="exp.latex"
                                @mouseup="logSelectionWithinInput($event, index)"
                                type="text"
                                class="input-field is--template-border w-input"
                                maxlength="256"
                                name="Latex"
                                data-name="Latex"
                                placeholder="Select dependant character"
                            >
                            <div v-show="exp.dependencyChar[0]">
                                <div
                                    class="state_dropdown-trigger"
                                    style="padding: 0.6rem 0rem;"
                                    v-for="(dep, element) in exp.dependencyChar"
                                    :key="element"
                                >
                                    <span>
                                        {{ exp.dependencyChar[element].character }} --> functionName(dependency = {{ exp.dependencyName ? exp.dependencyName: 'not set' }})
                                    </span>
                                    <span>
                                        <i @click="onDeleteDependencyChar(index, element)" class="fa fa-times"></i>
                                    </span>
                                </div>
                            </div>
                        </div>
                    </div>
                    <input @click="onSubmitComponent" type="submit" value="Submit" data-wait="Please wait..." class="button is--large w-button">
                </div>
                <div class="w-form-done">
                    <div>Thank you! Your submission has been received!</div>
                </div>
                <div class="w-form-fail">
                    <div>Oops! Something went wrong while submitting the form.</div>
                </div>
                </div>
            </div>
        </div>
        <u-modal
            v-if="showCompSelectModal"
            header="make component"
            body="do you want to make a new instance of the component?"
            v-on:close="(v) => {createNewInstance = v, showCompSelectModal = false}"
        ></u-modal>
    </div>
</template>

<script>
String.prototype.replaceAt = function(first_index, end_index, replacement) {
    return this.substr(0, first_index) + replacement + this.substr(end_index + replacement.length);
}

export default {
    name: "DesmosExpressionsComponent",
    props: {
        desmosUrls: {
            type: Array
        }
    },
    data: () => ({
        searchInput: '',
        resultComponents: [],
        arrowCounter: 0,
        stateComponent: '',
        createComp: false,
        title: '',
        description: '',
        componentType: '',
        existComponents: [],
        dependencyOptions: [],
        showCompSelectModal: false,
        createNewInstance: false,
        desmosId: 1
    }),
    components: {
        UfloSelect: () => import('./uflo-select.vue'),
        UModal: () => import('./u-modal.vue'),
        UfloSelectWithCreate: () => import('./uflo-select-with-create.vue'),
    },
    methods: {
        onSearchComponents: function (event) {
            this.title = this.searchInput;
            // get compponents list from api
            const components = [
                {
                    id:"inverse",
                    expressions: [{id:'inv', latex:'h\\left(x\\right)=\\frac{1}{x}'}],
                    title: "Simple Inverse Function",
                    description: "In mathematics, an inverse function is a function that &quot;reverses&quot; another function: if the function f applied to an input x gives a result of y, then applying its inverse function g to y gives the result x, i.e., g(y) = x if and only if f(x) = y."
                },
                {
                    id:"parabola",
                    expressions: [{id:'parabola', latex:'j\\left(x\\right)=x^{2}'}],
                    title: "Parabola",
                    description: "In mathematics, a parabola is a plane curve which is mirror-symmetrical and is approximately U-shaped."
                },
                {
                    id:"tangent2",
                    expressions: [
                        { id:'slider', latex:'q=2.4' },
                        {
                            id:'derivativeFunction',
                            latex: "s\\left(x\\right)=\\frac{d}{dx}f\\left(x\\right)",
                            dependencyName: "a",
                            dependencyOptions: ["a"],
                            dependencyChar: [{
                                character: 'x',
                                start: 28,
                                end: 28,
                            }]
                        },
                        {
                            id:'tagentLine',
                            latex: "y=s\\left(q\\right)\\left(x-q\\right)+f\\left(q\\right)",
                            dependencyName: "a",
                            dependencyOptions: ["a"],
                            dependencyChar: [{
                                character: 'x',
                                start: 34,
                                end: 34,
                            }]
                        },
                        {
                            id:'tagentPoint',
                            latex: "\\left(q,j\\left(q\\right)\\right)",
                            dependencyName: "a",
                            dependencyOptions: ["a"],
                            dependencyChar: [{
                                character: 'j',
                                start: 8,
                                end: 8,
                            }]
                        },
                    ],
                    title: "Tangent",
                    description: "In geometry, the tangent line to a plane curve at a given point is the straight line that &quot;just touches&quot; the curve at that point."
                },
                {
                    id:"integral",
                    expressions: [
                        {id:'intg-slider3', latex:'k=-2'},
                        {id:'intg-slider2', latex:'l=5'},
                        {
                            id:'integralArea',
                            latex: "\\min\\left(0,f\\left(x\\right)\\right)\\le y\\le\\max\\left(0,f\\left(x\\right)\\right)\\left\\{k\\le x\\le l\\right\\}",
                            dependencyName: "a",
                            dependencyOptions: ["a"],
                            dependencyChar: [
                                {
                                    character: 'x',
                                    start: 12,
                                    end: 12,
                                },
                                {
                                    character: 'x',
                                    start: 54,
                                    end: 54,
                                }
                            ]
                        },
                    ],
                    title: "Area/Integral",
                    description: "In mathematics, an integral assigns numbers to functions in a way that describes displacement, area, volume, and other concepts that arise by combining infinitesimal data. The process of finding integrals is called integration."
                }
            ];
            this.resultComponents = components.filter((comp) => {
                const isExist = 
                    comp.id.includes(this.searchInput) ||
                    comp.title.includes(this.searchInput) ||
                    comp.description.includes(this.searchInput);
                return isExist;
            });

            if (event.key === "Enter" && this.resultComponents[0]) {
                this.onClickToComponent(this.resultComponents[this.arrowCounter]);
            }
            if (event.key === "ArrowUp" && this.resultComponents[0]) {
                event.preventDefault()
                if (this.arrowCounter > 0) {
                    this.arrowCounter = this.arrowCounter - 1;
                    this.fixScrolling();
                }
            }
            if (event.key === "ArrowDown" && this.resultComponents[0]) {
                event.preventDefault()
                if (this.arrowCounter < this.resultComponents.length - 1) {
                    this.arrowCounter = this.arrowCounter + 1;
                    this.fixScrolling();
                }
            }
        },
        fixScrolling () {
            const liH = this.$refs.options[this.arrowCounter].clientHeight;
            this.$refs.scrollContainer.scrollTop = liH * this.arrowCounter;
        },
        onClickToComponent: function (comp) {
            const desmosLength = this.desmosUrls.length ? this.desmosUrls.length: 1;
            let desmosId = 1;
            if (desmosLength > 1) {
                desmosId = window.prompt(`insert desmos id equal or below ${desmosLength}`);
            } else {
                this.desmosId = 1;
            }
            if (+desmosId > 0 && (+desmosId == desmosLength || +desmosId < desmosLength)) this.desmosId = desmosId;
            if (comp.expressions.length <= 1) {
                window[`calculator${this.desmosId}`].setExpressions(comp.expressions);
            } else {
                // add dependent expressions
                this.stateComponent = comp;
                // reuse this component or create new dialog
                this.showCompSelectModal = true

                this.onActivateSelectMode()
            }
            //hides component bar 
            this.searchInput = '';
        },
        onActivateSelectMode () {
            const expIndex = this.stateComponent.expressions.findIndex((exp) => {
                return  exp.dependencyName && !exp.dependencyReplaced
            });
            if (expIndex >= 0) {
                document.getElementById(`calculator${this.desmosId}`).classList.add("is--selected");
                document.getElementById(`canvasInst${this.desmosId}`).classList.add("is--selected");
                //activates an animated selection mode through combo class
            } else {
                this.stateComponent.expressions.forEach((exp) => {
                    window[`calculator${this.desmosId}`].setExpressions([
                        {
                            id: exp.id,
                            latex: exp.latex,
                            // lineStyle: "DASHED"
                        },
                    ]);
                })
                //reset the visual selection mode
                this.stateComponent = '';
            }
        },
        onSelectExpressionFromDesmos (desmosId) {
            const id = window[`calculator${this.desmosId}`].selectedExpressionId;
            if (this.stateComponent && id && this.desmosId == desmosId) {
                //if the ID has been found 
                if (id) {
                    //deactivate visual selection mode 
                    document.getElementById(`calculator${this.desmosId}`).classList.remove("is--selected");
                    document.getElementById(`canvasInst${this.desmosId}`).classList.remove("is--selected");
                    //internal expression manipulation
                    const expressions = window[`calculator${this.desmosId}`].getExpressions()
                    //find the index of the selected expression in order to find its LaTex value
                    const index = expressions.findIndex(x => x.id === id);
                    if (expressions[index] && expressions[index].latex) {
                        const functionNameArr = expressions[index].latex.split('\\');
                        //extract its LaTex name
                        const functionName = functionNameArr[0];
    
                        // find index of first not replaced expression with dependency
                        const expIndex = this.stateComponent.expressions.findIndex((exp) => {
                            return exp.dependencyName && !exp.dependencyReplaced
                        });
    
                        //replace latex of all dependencies with same dependencyName
                        this.stateComponent.expressions.forEach((exp) => {
                            if (exp.dependencyName === this.stateComponent.expressions[expIndex].dependencyName) {
                                let expFunction = exp.latex
                                // replace with functionName
                                exp.dependencyChar.forEach((dchar) => {
                                    expFunction = expFunction.replaceAt(
                                        dchar.start,
                                        dchar.end,
                                        functionName
                                    );
                                });
    
                                // auto change functionName if user wants to create new instance
                                if (this.createNewInstance) {
                                    // auto change function name
                                    let index = 0;
                                    // find function name
                                    const expFuncName = expFunction.split('=')[0];
                                    const expFuncIndex = expFuncName.slice(expFuncName.indexOf('{'), expFuncName.indexOf('}') + 1)
                                    const expFuncNameWithoutIndex = expFuncName.replaceAt(expFuncName.indexOf('_{'), expFuncName.indexOf('}') + 1, '')
                                    expressions.forEach((expression) => {
                                        if (expression && expression.latex) {
                                            // find function same names 
                                            let funcName = expression.latex.split('=')[0];
                                            funcName = funcName.replaceAt(funcName.indexOf('_{'), funcName.indexOf('}') + 1, '')
                                            if (funcName === expFuncNameWithoutIndex) {
                                                index++
                                            }
                                        }
                                    })
                                    if (expFuncIndex && index > 0) {
                                        expFunction = expFunction.replace(expFuncIndex, `${expFuncIndex}${index}`)
                                    } else if (index > 0) {
                                        // replace after first element
                                        const funcNameFirst = expFunction.split('\\')[0]
                                        if (funcNameFirst) {
                                            expFunction = expFunction.replace(funcNameFirst, `${funcNameFirst}_{${index}}`)
                                        }
                                    }
                                }
                                exp.latex = expFunction;
                                exp.dependencyReplaced = true;
                            }
                        })
                        this.onActivateSelectMode()
                    }
                }
            }
        },
        async onCreateComp () {
            this.createComp = true;
            this.existComponents = [];
            const expressions = window[`calculator${this.desmosId}`].getExpressions()
            await expressions.forEach((exp) => {
                const latex = exp.latex;
                if(latex.substring(0, 3) === "C_{") {
                    this.existComponents.push({
                        ...exp,
                        dependencyChecked: false,
                        expressionSelected: false,
                        dependencyName: '',
                        dependencyChar: [],
                    })
                    this.dependencyOptions = []
                }
            })
            window.MathJax.typeset()
        },
        onClickCheckDependency (index, checkValue) {
            !checkValue ? checkValue = false: checkValue = true;
            this.existComponents[index].dependencyChecked = !checkValue
        },
        logSelectionWithinInput(e, index) {
            const selection = e.target.value.substring(
                e.target.selectionStart,
                e.target.selectionEnd
            );
            if (selection) {
                this.existComponents[index].dependencyChar.push({
                    character: selection,
                    start: e.target.selectionStart,
                    end: e.target.selectionEnd,
                })
            }
        },
        onDeleteDependencyChar (index, element) {
            this.existComponents[index].dependencyChar.splice(element, 1)
        },
        onSubmitComponent () {
            // send selected existComponents to server and add it in existed components list
            const selectedExpressions = this.existComponents.filter((ec) => {
                return ec.expressionSelected
            })

            const newComponent = {
                id: this.title,
                title: this.title,
                description: this.description,
                componentType: this.componentType,
                expressions: selectedExpressions
            }

            // if submitted
            console.log(newComponent.id + ' has submited')
            document.querySelector('.w-form-done').style.display = 'block'

            // if fail
            // console.log('an error in submition')
            // document.querySelector('.w-form-fail').style.display = 'block'

        },
        shortcutFuncs (event) {
            // focus by shift + b
            const modifier = event.ctrlKey || event.metaKey;
            if (modifier && (event.key === "b" || event.key === "B")) {
                this.$refs.search.focus()
            }
            if (event.key === 'Escape') {
                this.stateComponent = '';
                //deactivate visual selection mode 
                document.getElementById(`calculator${this.desmosId}`).classList.remove("is--selected");
                document.getElementById(`canvasInst${this.desmosId}`).classList.remove("is--selected");
            }
        }
    },
    created() {
        window.addEventListener("keyup", this.shortcutFuncs);
    },
    destroyed() {
        window.removeEventListener("keyup", this.shortcutFuncs);
    },
}
</script>

<style>

</style>
